let html = document.querySelector("#demo");
let style = document.querySelector("#style");
let string = `
/* 你好
 *我在画一个八卦图
 *先准备一个div
 */
#div1{
    border: 1px solid red;
    width: 200px;
    height: 200px;
}
/*画个八卦
 *先画个圆
 */
#div1{ 
    border-radius: 50%;
    box-shadow: 0px 0px 400px 78px rgba(11,1,1,0.75);
    border: none;
}
/*一黑一白
*/
#div1{
    background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 50%, rgba(0,0,0,1) 50%, rgba(0,0,0,1) 100%);
}
/*加俩奥里给
*/
#div1::before{
    width: 100px;
    height: 100px;  
    top:0;
    left:50%;
    transform: translateX(-50%);
    background: #000;
    border-radius: 50%;
    background: radial-gradient(circle, rgba(255,255,255,1) 25%, rgba(0,0,0,1) 25%);
}
#div1::after{
    width: 100px;
    height: 100px;   
    bottom:0;
    left:50%;
    transform: translateX(-50%);
    background: #fff;
    border-radius: 50%;
    background: radial-gradient(circle, rgba(0,0,0,1) 25%, rgba(255,255,255,1) 25%);
}
 `;
let string2 = "";
let n = 0;
let step = () => {
    setTimeout(()=>{
        //如果是回车，就不照搬
        //如果不是回车，就照搬
        if (string[n] === "\n") {
            string2 += "<br>";
        }else if(string[n] === " "){
            string2 += "&nbsp;" ; 
        }else{
            string2 += string[n];
        }
        dome.innerHTML = string2;
        style.innerHTML = string.substring(0, n);    
        window.scrollTo(0,99999); 
       dome.scrollTo(0,9999);            
        if(n < string.length - 1) {
            // 如果n不是最后一个，就继续
            n = n+1;
            step();
        }
         },50);
       };
      step(); //1=>2
     
  
